
@use 'theme';

@mixin chrome-sim {
    // height:634px; //set to 100%
    // max-height: 634px;
    // overflow: hidden;
}

body {
    margin:0px;
    padding: 0px;
    @include theme.font-text;
    font-size: theme.$font-base_size;
    color: theme.$text_color;
    // display: flex;
    // align-items: center;
    height:100vh;
    background-color: theme.$bg-default;

    img {
        image-rendering: pixelated;
    }

    img.cover {
        // padding: theme.$base-padding;

        width:100%;
        margin:auto;
        max-height: 40vh;
        object-fit: cover;
        
    }

    img.spinner {
        display: none;
    }

    .screen-defaults {
        width:100vw;
        height:100vh;
        display: flex;
        flex-direction: column;
        @include theme.font-text;
        font-size: theme.$font-base_size;
        background: theme.$bg-default;
        font-size: 150%;
        position: absolute;

        div.gradient {
            display: block;
            background: theme.$gradient-default;
        }
        
        h1 {
            font-weight: normal;
        }
    }

    .loading {
        display: none;
        opacity: 0;
        z-index: 500;
        background-color: rgba(33, 37, 41, 1) !important;
        text-align: center;
        position: absolute;

        img.spinner {
            display: block;
            width: 30vw;
            margin: auto;
            padding-top: 30vh;
        }

        .nes-container {
            width:80%;
            display: block;
            margin: auto !important;
            top: 35vh;
            height: 80px;
            font-size:120%;

            progress {
                height: 30px;
            }

            img {
                height:30px;
            }
            //background-color: theme.$bg-default;
        }
    }
}

.intro {
    width: 100%;
    height: 200%;
    display: block;
    background-color: theme.$bg-bad;
    position: absolute;
    z-index: 100;
    overflow: hidden;

    text-align: center;
    

    img.intro_title {
        width: 70%;
        margin-top: 40vh;
    }

    img.intro_graphics {
        width:170vw;
        margin-left: -10vw;
        margin-top: 60%;
    }

    div.menu {
        background-color: theme.$bg-default;
        padding-top: theme.$base-padding*5;
        padding-left: theme.$base-padding*3;
        padding-right: theme.$base-padding*3;
        padding-bottom: theme.$base-padding*5;
        text-align: left;
        h1 {
            font-weight: normal;
            font-size: 400%;
            text-align: center;
            line-height: 70%;
            margin-bottom: theme.$base-padding*3;
            color: white;
        }

        label {
        
            span {
                font-size: 200%;
            }

            input {
                margin-top:20px !important;
            }
        }

        button {
            font-size:200%;
            margin-bottom: 5%;
            line-height: 90%;
            width:100%;
            display: block;
        }
    }

    
}

.register {

    display:none;
    z-index: 450;

    img.cover {
        margin-top:-30px;
    }
    
    div.gradient {
        height: 20vh;
    }


    .nes-field {
        background-color: theme.$bg-default-light;
        padding: theme.$base-padding*2;
        padding-bottom: 30px;

        p{
            line-height: 100%;
        }

        input {
            background-color: theme.$bg-default;
        }

        ::placeholder {
            color:rgba(255,255,255,.4);
        }

        button {
            margin-top:30px;
            
            width:100%;
        }
    }
}


.highscore {

    justify-content: center;
    height:auto !important;
    min-height: 100% ;
    padding-bottom: 15vh;

    h1 {
        text-align: center;
    }

    img.cover {
        margin-top:-30px;
    }

    div.gradient {
        height: 20vh;
        width: 100%;
        position: fixed;
        bottom: 0px;
        z-index: 100;
    }

    button {
        position: fixed;
        bottom: 0px;
        width: 90%;
        margin-left: 5%;
        margin-bottom: 5%;
        z-index: 300;
    }

    table {
        width:90%;
        flex-grow: 2;
        margin: auto !important;
        background-color: theme.$bg-default !important;
        table-layout: auto;

        font-size: 100%;

        td {
            text-align: center;
            padding:10px;
        }

        // tr:nth-child(even) td {
        //     background: theme.$bg-default-light !important;
        // }

        tr td:first-child {
            width: 10vw !important;
        }

      
    }
}

#app {
    background-color: theme.$bg-default;
    position: absolute;
    width:100vw;
    height:100vh;
    @include chrome-sim;
    display: none;
}

section.statusbar {
    display: flex;
    // border: 1px solid red;
    width:100%;
    padding: theme.$base-padding;
    flex-direction: row;
    flex-flow: space-between;
    align-items: center;
    height: theme.$status-height;
    position: absolute;
    z-index: 200;
    height: 5%;
    
    ul {
        margin: 0px;
        margin-right: 20px;
        padding: 0px;
        width: 100%;
        list-style-type: none;
        display: flex;
        align-content: stretch;

        li {
            flex-grow: 1;
            height:5px;
            background-color: white;
            margin-right: 2px;
            opacity: .2;

            &.done {
                opacity: .6;
            }

            &.active {
                opacity: .8;
            }
        }
    }

    div {
        // width: 80px;
        display: flex;
        align-items: center;
        

        img.coin {
            
            width: 25px;
            display: inline-block;
        }

        span {
            display: block;
            @include theme.font-numbers;
            font-size: 80%;
            text-align: right;
            
        }
    }


}

section.screen {
    display: flex;
    flex-direction: column;
    @include theme.font-text;
    font-size: theme.$font-base_size;
    background: theme.$bg-default;
    padding-top: theme.$status-height;
    width:100%;
    z-index: 1;
    height:100vh;
    position: fixed;
    align-items: flex-start;
    @include chrome-sim;

    div.text_wrapper {
        display: flex;
        flex-direction: column;
        width:100%;
        height:100%;
    }
   
    h1 {
        padding-left: theme.$base-padding;
        padding-right: theme.$base-padding;
        margin:auto;
        font-size:150%;
        font-weight: normal;
        width: 70%;
        font-variant-numeric: tabular-nums lining-nums;

        display: flex;
        align-items: center;
        justify-content: space-between;

        font-size: 350%;
        text-align: right;

        img {
            // display:none;
            width:13vw;
            display: inline-block;
        }
       
    }

    div.text {
        padding-left: theme.$base-padding;
        padding-right: theme.$base-padding;
        flex-grow: 2;
        margin:auto;
        margin-top:10px;
        width: 90%;
        line-height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        .nes-container {
            background-color: transparent;

            h3.title {
                background-color: theme.$bg-default;
                margin-top: -1.5rem !important;
                font-size: 75%;
                margin-bottom: 0px;
            }
            
            &.popout {
                width:115%;
                margin-left:-7%;
            }
        }
    }



    &.positive  {
        background: theme.$bg-good;

        div.gradient {
            background: theme.$gradient-good;
        }

        h1 {

            img {
                display: inline-block;
            }
            font-size: 350%;
            text-align: right;
        }

        // .nes-container

        div.buttons {background: theme.$bg-good-light;}
    }

    &.negative  {

        background: theme.$bg-bad;

        div.gradient {
            background: theme.$gradient-bad;
        }

        h1 {
            img {
                display: inline-block;
            }
            font-size: 350%;
            text-align: center;
        }

        div.buttons { background: theme.$bg-bad-light;}
    }


    div.gradient {
        display: block;
        background: theme.$gradient-default;
        height:3vh;
    }

    div.buttons {
        display: flex;
        flex-direction: column;
        padding: theme.$base-padding;
        background: theme.$bg-default-light;
    }

    button {
        font-size:100%;
        margin-bottom: 2%;
        line-height: 90%;
    }


}

@media screen and (min-width: 1024px){
    
    section.screen {
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    .text_wrapper.nes-container.is-rounded.is-dark{
        background-color: transparent;
    }

    section.screen div.text_wrapper {
        height: fit-content;
    }

    div.text{
        padding-bottom: 10%;
    }

    //flexbox - children layout
    section.screen img.cover,
    .text_wrapper {
        width:30%;
        max-width: 480px;
    }


    section.screen img.cover {
        margin: 0;
        object-fit: contain;
        margin-right:2.5%;
    }

    
    //response img size fix (positive, negative)
    section.screen.positive h1 img,
    section.screen.negative h1 img{
        width: 25%;
        max-width: 100px;
    }

    section.statusbar {
        left: 10%;
        max-width: 80%;
    }

 
    //button to the bottom
    .button_wrapper {
        position: absolute;
        bottom:0px;
        left: 0px;
        width: 100%;
        height:15%;
        display: inline-block;
    }

    section.screen div.buttons {
        flex-direction: row;
        justify-content: center;
        padding-bottom: 0.5%;
        position: relative;
    }

    div.buttons{
        height: 100%;
        min-height: 100px;
    }

    .gradient{
        position: absolute;
        width: 100%;
        left:0;
        bottom:15%;
    }

    .nes-btn {
        width: 38%;
        max-height: 90px;
    }

    .nes-btn + .nes-btn{
        margin-left: 4%;
    }

    .nes-container.is-dark {
        position: static !important;
    }

    .intro {
        width: 100%;
        height: fit-content;
        display: block;
        background-color: theme.$bg-bad;
        position: absolute;
        z-index: 100;
        overflow: hidden;
    
        text-align: center;
        
    
        img.intro_title {
            width: 50%;
            margin-top: 40vh;
        }
    
        img.intro_graphics {
            width:170vw;
            margin-left: -10vw;
            margin-top: 60%;
        }
    
        div.menu {
            background-color: theme.$bg-default;
            padding-top: theme.$base-padding*5;
            padding-left: theme.$base-padding*3;
            padding-right: theme.$base-padding*3;
            padding-bottom: theme.$base-padding*5;
            text-align: left;
            h1 {
                font-weight: normal;
                font-size: 200%;
                text-align: center;
                line-height: 70%;
                margin-bottom: theme.$base-padding*3;
                color: white;
            }
    
            label {
            
                span {
                    font-size: 200%;
                }
    
                input {
                    margin-top:20px !important;
                }
            }
    
            button {
                font-size:200%;
                margin-bottom: 5%;
                line-height: 90%;
                width:100%;
                display: block;
            }
        }
    
        
    }


    

    



  
    
}